import Vue from "vue";
import VueRouter from "vue-router";

import Story1 from "../views/Story1";
import Story2 from "../views/Story2"
import Story3 from "../views/Story3";
import Story4 from "../views/Story4";
import Story5 from "../views/Story5";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Story1",
    component: Story1
  },
  {
    path: "/02",
    name: "Story2",
    component: Story2
  },
  {
    path: "/03",
    name: "Story3",
    component: Story3
  },
  {
    path: "/04",
    name: "Story4",
    component: Story4
  },
  {
    path: "/05",
    name: "Story5",
    component: Story5
  },
];

const router = new VueRouter({
  mode: "history",
  base: '/',
  routes
});

export default router;

export const strict = false;
